#root,
html,
body {
  height: 100%;
    background-color: rgba(211, 211, 211, 0.43);
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 200;
  text-rendering: optimizeLegibility;
  background-color: #F8F8FA;
}

h2 {
  font-size: 24px;
  font-weight: 600;
  line-height: 34px;
  margin: 5px 0;
}

h3 {
  font-size: 20px;
  font-weight: 400;
  line-height: 27px;
  margin: 5px 0;
}

ul,
li {
  list-style: none;
  padding-left: 0;
}

a {
  text-decoration: none;
  color: #000;
  opacity: 1;
  transition: opacity 0.25s ease-in-out;
}

a:hover {
  opacity: 0.35;
  text-decoration: none;
}

a:active {
  text-decoration: none;
}

pre {
  white-space: pre-wrap;
}

.films-analysis-service {
  height: 112px;
  width: 460px;
  color: #7D7DA7;
  font-size: 48px;
  font-weight: bold;
  letter-spacing: 0;
  line-height: 56px;
  margin: auto;
  padding-top: 132px;
  padding-bottom: 44px;
}

.search-form {
    text-align: center;
    margin: auto;
    width: 460px;
}

.search-form input {
    border: 0;
    border-radius: 15px;
    width: 100%;
    outline: none;
    font-size: 20px;
    padding: 15px 30px;
}

.search-form button {
    border: 0;
    background-color: #FFBB4B;
    padding: 10px 20px;
    border-radius: 15px;
    color: white;
    font-size: 20px;
    margin-top: 50px;
}

.search-form button.disabled {
    background-color: gray;
    cursor: not-allowed;
}

.cards {
    margin: 50px auto 0;
    width: 460px;
    display: grid;
    grid-template-columns: 6fr 6fr;
    grid-column-gap: 16px;
}

.cards .card {
    padding: 15px;
    background-color: white;
    font-size: 20px;
    margin-bottom: 16px;
    font-weight: 500;
    border-radius: 15px;
}

.cards .card label {
    display: block;
    color: gray;
    font-size: 16px;
    margin-bottom: 30px;
}
